import Vue from 'vue';
import Router from 'vue-router';
import Phase from './views/Phase.vue';
import Matches from './views/Matches.vue';
import Stadiums from './views/Stadiums.vue';
import Login from './views/Login.vue';
import MyBets from './views/MyBets.vue';
import PublicBets from './views/PublicBets.vue';
import firebaseController from './firebaseController';
import * as firebase from "firebase";

Vue.use(Router);

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'phase',
            component: Phase,
        },
        {
            path: '/matches',
            name: 'matches',
            component: Matches,
            meta: {requiresAuth: true}
        },
        {
            path: '/stadiums',
            name: 'stadiums',
            component: Stadiums,
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/my-bets',
            name: 'myBets',
            component: MyBets,
            meta: {requiresAuth: true}
        },
        {
            path: '/public-bets',
            name: 'publicBets',
            component: PublicBets,
            meta: {requiresAuth: true}
        }
    ],
});


router.beforeEach((to, from, next) => {
    let currentUser = firebase.auth().currentUser;
    let requiresAuth = to.matched.some(rec => rec.meta.requiresAuth);

    if (requiresAuth && !currentUser) {
        next('login');
    }

    else {
        firebaseController.refreshData().then(()=>{
            next();
        })
    }
})

export default router;
