<template>
    <div>
        <tr :id="'match_' + game.name">
            <td :class="gameclass + '--date'" class="text-center moment-date" v-text="viewDate"></td>
            <td :class="homeclass + ' ' + gameclass + '--hometeam'" class="text-right">
                <teamname :teamId="game.home_team"></teamname>

            </td>
            <td :class="gameclass + '--spacer'" class="text-center" :title="game.stadium.name">
                <small v-text="'Match ' + game.name"></small>
            </td>
            <td :class="awayclass + ' ' + gameclass + '--awayteam'">

                <teamname :teamId="game.away_team"></teamname>

            </td>
        </tr>
        <tr v-for="(score, user) in scores">
            {{score[0]}} - {{score[1]}} {{users[user].username}}
        </tr>
    </div>
</template>

<script>
    import Teamname from './Teamname.vue';
    import functions from '../static';
    import * as moment from 'moment';
    import {Moment} from 'moment';
    import firebaseController from '../firebaseController';

    export default {
        props: {

            game: {
                type: '',
                required: true,
            },
            gametype: {
                type: String,
                required: true,
                default: 'groups',
            },
            scores: {
                type: Object
            }
        },

        created() {
            firebaseController.getUsers().then(res => {
                this.users = res;
            })
        },
        computed: {
            gameclass() {
                if (this.gametype !== 'groups') {
                    return 'table-knockouts';
                }
                return 'table-' + this.gametype;
            },
            viewDate() {
                return moment(this.game.date).from(new Date());
            },
            titledate() {
                return functions.titleDate(this.game.date);
            },
            finished() {
                return this.game.finished;
            },
            finishedclass() {
                return this.game.finished ? this.gameclass + '--finished' : '';
            },
            homeclass() {
                if (this.game.finished) {
                    if (this.game.homeResult === this.game.awayResult) {
                        return this.gameclass + '--draw';
                    }
                    if (this.game.homeResult > this.game.awayResult) {
                        return this.gameclass + '--winner';
                    }
                    if (this.game.homeResult < this.game.awayResult) {
                        return this.gameclass + '--loser';
                    }
                }
                return '';
            },
            awayclass() {
                if (this.game.finished) {
                    if (this.game.homeResult === this.game.awayResult) {
                        return this.gameclass + '--draw';
                    }
                    if (this.game.homeResult < this.game.awayResult) {
                        return this.gameclass + '--winner';
                    }
                    if (this.game.homeResult > this.game.awayResult) {
                        return this.gameclass + '--loser';
                    }
                }
                return '';
            },
        },
        data() {
            return {
                users: {}
            }
        },
        components: {
            Teamname,
        },
    };
</script>

<style>
    .locked {
        opacity: .4;
        background-color: grey;
    }
</style>
